/* ---- MAIN PAGE ---- */
.welcome-page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #0a0a0a, #1a002b);
  text-align: center;
}

/* ---- BUNNY IMAGE ---- */
.bunny-img {
  width: 180px;
  height: auto;
  margin-bottom: 20px;

  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%   { transform: translateY(0); }
  50%  { transform: translateY(-10px); }
  100% { transform: translateY(0); }
}

/* ---- TEXT ---- */
.welcome-text {
  font-size: 1.8rem;
  font-weight: bold;
  color: white;
  margin-bottom: 30px;
}

/* ---- BUTTON ROW ---- */
.button-row {
  display: flex;
  gap: 40px;
}

/* BUTTONS */
.btn {
  padding: 10px 26px;
  border-radius: 10px;
  font-size: 1rem;
  cursor: pointer;
  border: none;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.yes {
  background: #b47fff;
  color: white;
}

.no {
  background: #2e0147;
  color: #e5d4ff;
  border: 1px solid #b47fff;
}

/* Hover */
.btn:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 16px rgba(255, 255, 255, 0.2);
}