.exit-page {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #0a0a0a, #1a002b);
  text-align: center;
  color: white;
}

/* Exit Bunny */
.exit-img {
  width: 180px;
  height: auto;

  animation: wave 1.8s infinite ease-in-out;
}

@keyframes wave {
  0% { transform: rotate(0deg); }
  50% { transform: rotate(6deg); }
  100% { transform: rotate(0deg); }
}

/* Title */
.exit-title {
  font-size: 2rem;
  margin-top: 20px;
}

/* Text */
.exit-text {
  color: #d4c4ff;
  margin-bottom: 20px;
}

/* Button */
.exit-btn {
  padding: 12px 28px;
  background: #b47fff;
  color: white;
  border-radius: 10px;
  border: none;
  cursor: pointer;
  text-decoration: none;
  transition: 0.2s ease;
}

.exit-btn:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 18px rgba(255,255,255,0.2);
}