.about-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #0a0a0a, #1a002b);
  color: white;
  padding: 40px 20px;
  text-align: center;
}

/* About Bunny Image */
.about-img {
  width: 200px;
  height: auto;
  margin: 0 auto 20px auto;

  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%   { transform: translateY(0); }
  50%  { transform: translateY(-10px); }
  100% { transform: translateY(0); }
}

/* Heading */
.about-title {
  font-size: 2.3rem;
  font-weight: bold;
  color: #d6b4ff;
  margin-top: 20px;
}

/* Text */
.about-text {
  max-width: 600px;
  margin: 20px auto;
  font-size: 1.1rem;
  line-height: 1.6;
  color: #e9d8ff;
}